<template>
  <device-info v-if="!isAndon" :back="back" :deviceData="deviceData"></device-info>
  <andon-device-info v-else :back="back" :deviceData="deviceData">></andon-device-info>
</template>
<script>
import deviceInfo from '../EquipmentStatus/deviceInfo.vue';
import andonDeviceInfo from '../EquipmentStatus_andon/deviceInfo.vue';
import { ISANDON } from '@/utils/ajaxConfigure';
export default {
  name: 'deviceStatusInfo',
  components: {
    deviceInfo,
    andonDeviceInfo
  },
  data() {
    return {
      deviceData: {},
      isAndon: ISANDON
    };
  },
  created() {
    this.type = this.getUrlKeyMain('type', window.location.href);
    if (this.type === 'deviceJump') {
      const deviceData = localStorage.getItem('deviceJump');
      this.deviceData = JSON.parse(deviceData);
    }
  },
  methods: {
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    back() {
      this.$router.push('/MainControlRoom');
    }
  }
};
</script>

